 import '../../static/css/warn/iconfont.css'
import Head from './head'
 import Loading from './Loading'
  export default {
      name: "authentication",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          data:{
           token: localStorage.getItem('token')
          },
          stepNum: '10' + '%',
          isStep:1,
          options:[
            {label:'中国',value:'0'}
          ],
          cuntry:'',
          radio:"",
          imageUrl1:'',
          imageUrl:'',
          firstId:'',
          secendId:'',
          huzhaoId:'',
          photo:"",
          isPhoto:'',
          isFirst:false,
          isSeccond:false,
          srcData:'',
          isWait:false,
          active_id:'',
          sd:''
        }
    },
    components:{
        Head,
      Loading
    },
    watch:{
      photo:function(){
        if(this.photo == '1'){
          this.$nextTick(()=>{
            this.takePhoto()
          })

        }
      }
    },
    methods:{
        //上传进度
      uploadLoading(file,fileList){
      
        if(file.status == 'ready'){
          this.isFirst = true
        }else if(file.status == 'success'){
          this.isFirst = false
        }
      },
      uploadScecond(file,fileList){
      
        if(file.status == 'ready'){
          this.isSeccond = true
        }else if(file.status == 'success'){
          this.isSeccond = false
        }
      },
      changeStep(val){
        if(val == 1){
          this.stepNum = '30' + '%'
          this.isStep = 2
        }else if(val == 2){
          this.stepNum = '50' + '%'
          this.isStep = 3
        }else if(val == 3){
          if(!this.cuntry.length){
            this.$message({
              type: 'error',
              message: '请选择国家!'
            })
          }else if(!this.radio){
            this.$message({
              type: 'error',
              message: '您还没有选择类型'
            })
          }else if(this.radio == '1'){
            this.stepNum = '70' + '%'
            this.isStep = 4
          }else{
            this.stepNum = '70' + '%'
            this.isStep = 5
          }
        }else if(val == 4){
          this.checkSfz()
        }else if(val == 5){
          this.checkHz()
        }else if(val == 6){
          this.isWait = true
          this.$http.post(this.api + '/UploadB',{
            token: localStorage.getItem('token'),
            file: this.photo == 1? this.isPhoto: this.srcData,
            flag:1,
            iswater:1
          })
            .then(res=>{
              if(res.data.code == 1){
               let imageId = res.data.data.image_id
                this.sendPlayer(imageId)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.changeStep(val)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
                this.isWait = false
              }
            })
        }else if(val == 7){
          if(this.active_id){
            this.$router.push('/publishList')
          }else{
            this.$router.push('/')
          }
        }
      },
      sendPlayer(msg){
        this.$http.post(this.api + '/IdentifyCardPlanner',{
          token: localStorage.getItem('token'),
          issubmit:1,
          face_image:msg,
          flag:3
        })
          .then(res=>{
            if(res.data.code == 1){
              this.isWait = false
              if(this.active_id){
                this.sendTIan()
              }
              this.isStep = 7
              this.stepNum = '100' + '%'
              this.$message({
                type:'success',
                message:'提交成功！'
              })
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendPlayer(msg)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isWait = false
            }
          })
          .catch(error=>{
            this.isWait = false
          })
      },
      sendTIan(){
        this.$http.post(this.api + '/home/Activity/save_activity',{
          token: localStorage.getItem('token'),
          activity_id: this.active_id,
          complete: 1,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '体验提交成功！'
              })
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendTIan()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      goBack(val){
        if(val == 5){
          this.isStep = 3
          this.stepNum = '70' + '%'
        }else if(val == 1){
          this.isStep = 'volunteer'
          this.stepNum = '5' + '%'
        }else{
          this.isStep -= 1
          this.stepNum = parseInt(this.stepNum) - 20 +'%'
        }
      },
      firstHandleAvatarSuccess(res,file){
        if(res.code == 1){
          this.imageUrl1 = res.data.domain + res.data.image_url
          this.firstId = res.data.image_id
        }else{
          alert('上传失败！请重新上传')
        }
      },
      handleAvatarSuccess(res, file) {
        if(res.code == 1){
          this.imageUrl = res.data.domain + res.data.image_url
          this.secendId = res.data.image_id
        }else{
          alert('上传失败！请重新上传')
        }
      },
      uploadPhoto(file){
        let _this = this
     
        var File = file.target.files[0]
        var reader = new FileReader();
        reader.readAsDataURL(File);
        reader.onload = function (e) {
          _this.srcData = e.target.result;
          _this.changeStep(6)
        }
      },
      huzhaoSuccess(res){
        if(res.code == 1){
          this.imageUrl = res.data.domain + res.data.image_url
          this.huzhaoId = res.data.image_id
        }else{
          alert('上传失败！请重新上传')
        }
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg'|| file.type === 'image/png' || file.type === 'image/jpg';
        const isLt10M = file.size / 1024 / 1024 < 10;
        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG/JPEG/PNG 格式!');
          this.isFirst = false
          this.isSeccond = false
        }
        if (!isLt10M) {
          this.$message.error('上传头像图片大小不能超过 10MB!');
          this.isFirst = false
          this.isSeccond = false
        }
        return isJPG && isLt10M;
      },
      checkSfz(){
        this.isWait = true
        this.$http.post(this.api + '/IdentifyCardPlanner',{
          token: localStorage.getItem('token'),
          idcard_z: this.firstId,
          idcard_f: this.secendId,
          flag:1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.sd = '身份证'
              this.stepNum = '80' + '%'
              this.isStep = 6

              this.isWait = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.checkSfz()
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isWait = false
            }
          })
          .catch(error=>{
            this.isWait = false
          })
      },
      checkHz(){
        this.isWait = true
        this.$http.post(this.api + '/IdentifyCardPlanner',{
          token: localStorage.getItem('token'),
          passport: this.huzhaoId,
          flag:2
        })
          .then(res=>{
            if(res.data.code == 1){
              this.sd = '护照'
              this.stepNum = '80' + '%'
              this.isStep = 6

              this.isWait = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.checkSfz()
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isWait = false
            }
          })
          .catch(error=>{
            this.isWait = false
          })
      },
      takePhoto(){
        let _this = this
        var mediaStreamTrack;
          navigator.mediaDevices.getUserMedia = navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia || navigator.mediaDevices.mozGetUserMedia;
        if (navigator.getUserMedia) {
            navigator.getUserMedia({ audio: true, video: { width: 500, height: 400 } },
              function(stream) {
                mediaStreamTrack = typeof stream.stop === 'function' ? stream : stream.getTracks()[1];
                video.srcObject = stream
                video.play();
              },
              function(err) {
                alert("The following error occurred: " + err.name)
              }
            );
          } else {
            alert('getUserMedia not supported')
          }
          document.getElementById("close").addEventListener("click", function () {
          mediaStreamTrack && mediaStreamTrack.stop();
          _this.photo = ''
        });
          document.getElementById("closel").addEventListener("click", function () {
          mediaStreamTrack && mediaStreamTrack.stop();
          _this.photo = ''
        });
        document.getElementById("picture").addEventListener("click", function () {
          var canvas = document.getElementById("canvas")
          canvas.style.display = 'block'
          var context = document.getElementById("canvas").getContext("2d")
          context.drawImage(video, 0, 0, 500, 400);
          _this.isPhoto = canvas.toDataURL("image/png");
           var newPhoto = document.getElementById('newPhoto')
          newPhoto.style.display = 'block'
           var refalse = document.getElementById('refalse')
          refalse.style.display = 'block'
          var picture = document.getElementById('picture')
          picture.style.display = 'none'
        });
        document.getElementById('refalse').addEventListener('click',function(){
          _this.isPhoto = ''
          var refalse = document.getElementById('refalse')
          var canvas = document.getElementById("canvas")
          var newPhoto = document.getElementById('newPhoto')
          var picture = document.getElementById('picture')
          picture.style.display = 'block'
          newPhoto.style.display = 'none'
          refalse.style.display = 'none'
          canvas.style.display = 'none'
        })
      },
    },
    mounted(){

    },
    created(){
        this.active_id = this.$route.query.active_id
    }
  }



